	module.exports = "<div> <div class=\"github-search panel panel-default\"> <div class=panel-heading> <v-select :debounce=250 :value.sync=repo :options=options :on-search=getOptions placeholder=\"Search GitHub Repositories...\" label=full_name></v-select> </div> <div class=panel-body v-if=repo> <img :src=repo.owner.avatar_url alt=\"{{ repo.owner.login }}\" class=gravatar> <p>{{ repo.description }}</p> </div> <div class=panel-footer v-if=repo> <ul class=list-inline role=tablist> <li><a href=\"{{ repo.url }}\">Stars <span class=badge>{{ repo.stargazers_count }}</span></a> </li> <li><a href=\"{{ repo.url }}\">Forks <span class=badge>{{ repo.forks }}</span></a></li> <li><a href=\"{{ repo.url }}\">Open Issues <span class=badge>{{ repo.open_issues_count }}</span></a></li> </ul> </div> </div> <div v-if=error class=\"alert alert-warning\" role=alert> <button type=button class=close @click=\"error = null\"> <span aria-hidden=true>&times;</span></button> {{ error.message }} </div> </div>";

