	module.exports = "<div class=\"dropdown v-select\" :class=dropdownClasses> <div v-el:toggle @mousedown.prevent=toggleDropdown class=\"dropdown-toggle clearfix\" type=button> <span class=form-control v-if=\"!searchable && isValueEmpty\"> {{ placeholder }} </span> <span class=selected-tag v-for=\"option in valueAsArray\" track-by=$index> {{ getOptionLabel(option) }} <button v-if=multiple @click=select(option) type=button class=close> <span aria-hidden=true>&times;</span> </button> </span> <input v-el:search :debounce=debounce v-model=search v-show=searchable @keydown.delete=maybeDeleteValue @keyup.esc=onEscape @keydown.up.prevent=typeAheadUp @keydown.down.prevent=typeAheadDown @keyup.enter.prevent=typeAheadSelect @blur=\"open = false\" @focus=\"open = true\" type=search class=form-control :placeholder=searchPlaceholder :style=\"{ width: isValueEmpty ? '100%' : 'auto' }\"> <i v-el:open-indicator role=presentation class=open-indicator></i> <slot name=spinner> <div class=spinner v-show=\"onSearch && loading\">Loading...</div> </slot> </div> <ul v-el:dropdown-menu v-show=open :transition=transition class=dropdown-menu :style=\"{ 'max-height': maxHeight }\"> <li v-for=\"option in filteredOptions\" track-by=$index :class=\"{ active: isOptionSelected(option), highlight: $index === typeAheadPointer }\" @mouseover=\"typeAheadPointer = $index\"> <a @mousedown.prevent=select(option)> <template v-if=partial> <partial :name=partial></partial> </template> <template v-else> {{ getOptionLabel(option) }} </template> </a> </li> <li transition=fade v-if=!filteredOptions.length class=divider></li> <li transition=fade v-if=!filteredOptions.length class=text-center> <slot name=no-options>Sorry, no matching options.</slot> </li> </ul> </div>";

